import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {DesktopPriceBox, MobilePriceBox} from "eshop/modules/cart/components/entriesList/shared/PriceBoxes";
import {ResponsiveVisibility} from "eshop/modules/core/components/ui/RWDUtils";
import {Section, Trigger} from "eshop/modules/core/components/ui/Expander";
import OnlineUtils from "eshop/utils/OnlineUtils";

const LRow = props => {
    let selectedClassName = props.selected ? "g-brand2-bdrc" : "g-white1-bdrc";
    return (<div
        className={"l-row u-spacing-s u-small-padding-top-m u-small-padding-m u-border u-border--l " + selectedClassName}>
        {props.children}
    </div>)
}

const LCol = props => {
    let bigColumnWidth = props.big ? " l-col-" + props.big : "";
    let mediumColumnWidth = props.medium ? " l-col-medium-" + props.medium : "";
    let smallColumnWidth = props.small ? " l-col-small-" + props.small : "";
    let className = props.className ? props.className : "";
    return (<div className={"l-col " + bigColumnWidth + mediumColumnWidth + smallColumnWidth + " " + className}>
        {props.children}
    </div>);
}

LCol.propTypes = {
    big: PropTypes.string,
    medium: PropTypes.string,
    small: PropTypes.string,
    className: PropTypes.string
}

export default class VASBox extends Component {
    constructor(props) {
        super(props);
    }

    transformPriceInfo(prices) {
        var monthlyPrices = prices.map((charge) => {
            let priceStr = OnlineUtils.formatPrice(charge.price)
            return ({
                integer: priceStr.split(',')[0],
                fraction: priceStr.split(',')[1],
                currency: charge.currency,
                start: charge.monthFrom,
                end: charge.monthTo
            });
        });
        return monthlyPrices;
    }


    getSectionHeader() {
        return (
            <div>
                <Trigger className="js-expander__trigger__nested u-inline u-text-nowrap">
                    <span className="js-expander__trigger--show">{'Zwiń\u00a0szczegóły'}</span>
                    <span className="js-expander__trigger--hide">{'Szczegóły'}</span>
                </Trigger>
            </div>
        );
    }

    changeSelectionForRadio() {
        console.log("VAS changeSelectionForRadio");

        this.props.onSelectionChanged(this.props.vas);
    }


    render() {
        let icon = this.props.vas.thumbnailIcon ? this.props.vas.thumbnailIcon : "air-drop";
        return (
            <LRow selected={this.props.vas.selected}>
                <LCol big="1" medium="1" small="2" className="u-large-padding-top-s u-medium-padding-top-s">
                    <div
                        className="u-large-padding-all-m u-large-padding-top-l u-medium-padding-top-l u-medium-padding-all-m u-small-padding-top">
                        {this.props.multiChoice ?
                            <label className="o-checkbox opl-checkbox">
                                <input type="checkbox" className="switcher-trigger" checked={this.props.vas.selected}
                                       onChange={this.props.onSelectionChanged}
                                       disabled={this.props.vas.disabled}/>
                                       <span className="o-ci"/>
                            </label> :
                            <OraVasRadio {...this.props} />
                        }
                    </div>
                </LCol>
                <LCol small="3" medium="2" big="2">
                    <div className="u-padding-all-m u-text-center u-small-no-padding">
                        <span className={"g-icon g-icon--only g-icon--" + icon}/>
                    </div>
                </LCol>
                <LCol small="7" medium="6" big="6" className="u-small-no-padding-b">
                    <ResponsiveVisibility desktop={false} mobile={true}>
                        <div className="u-padding-all-m u-small-no-padding">
                            <p className="u-font-bold">{this.props.vas.title}</p>
                            {this.props.vas.slogan && <p dangerouslySetInnerHTML={{__html: this.props.vas.slogan}}></p>}
                            <MobilePriceBox
                                prices={this.transformPriceInfo(this.props.vas.paymentDescriptions.monthlyPayments)}/>
                        </div>
                    </ResponsiveVisibility>
                </LCol>
                <LCol small="12" medium="5" big="5" className="u-small-no-padding-t">
                    <div className="u-padding-all-m u-small-no-padding">
                        <p className="u-font-bold u-small-hide">{this.props.vas.title}</p>
                        {this.props.vas.slogan && <p className="u-small-hide" dangerouslySetInnerHTML={{__html: this.props.vas.slogan}}></p>}
                        <Section header={this.getSectionHeader()}
                                 contentClassName="js-expander__content__nested u-hide--soft" headerBelow={true}
                                 className="opl-section-outer">
                            <div aria-hidden="false" className="u-padding u-padding-top"
                                 dangerouslySetInnerHTML={{__html: this.props.vas.detailsDescription || this.props.vas.longDescription}}/>
                        </Section>
                    </div>
                </LCol>
                <LCol big="4" medium="4" className="u-small-no-padding-t">
                    <ResponsiveVisibility mobile={false}>
                        <DesktopPriceBox
                            prices={this.transformPriceInfo(this.props.vas.paymentDescriptions.monthlyPayments)}
                            biggerPadding={false}/>
                    </ResponsiveVisibility>
                </LCol>
            </LRow>);
    }
}

VASBox.propTypes = {
    vas: PropTypes.shape({
        title: PropTypes.string,
        id: PropTypes.string,
        paymentDescriptions: PropTypes.shape({
            monthlyPayments: PropTypes.array
        })
    }),
    idx: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    onSelectionChanged: PropTypes.func
}
VASBox.defaultProps = {
    multiChoice: true

}
class OraVasRadio extends Component {

    constructor(props){
        super(props);
    }
    getPropsForRadio(text, value) {
        return {
            name: "retentionBonuses",
            text: text,
            value: value,
            labelClassName: "o-radio opl-radio",
            readOnly: false,
            checked: !!(value == this.props.vas.selected),
            onChange: this.changeSelectionForRadio.bind(this)
        };
    }

    render(){
        return(
            <label className="o-radio opl-radio">
                <input type="radio" className="switcher-trigger"  name={"retentionBonuses"}
                       checked={ !!this.props.vas.selected}
                       onClick={this.props.onSelectionChanged}/>
                       <span className="o-ci"/>
            </label>

        )

    }

}
class Checkbox extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        <label className="o-checkbox opl-checkbox">
            <input type="checkbox" className="switcher-trigger" checked={this.props.vas.selected}
                   onClick={this.props.onSelectionChanged}/>
                   <span className="o-ci"/>
        </label>
    }
}